.makes__container {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1440px) {
  .makes__container {
    padding-block-start: 225px;
  }
}
@media (max-width: 1025px) {
  .makes__container {
    padding-block-start: 312px;
  }
}
@media (max-width: 992.99px) {
  .makes__container {
    padding-block-start: 117px;
  }
}

.makes__body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-block-end: 48px;
}
@media (max-width: 1025px) {
  .makes__body {
    padding-block-end: 68px;
  }
}
@media (max-width: 992.99px) {
  .makes__body {
    text-align: center;
    padding-block-end: 48px;
  }
}
@media (max-width: 992.99px) and (max-width: 600px) {
  .makes__body {
    text-align: start;
    align-items: flex-start;
  }
}

.makes__body-title {
  padding-block-end: 16px;
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: -1px;
  max-width: 650px;
}
@media (max-width: 992.99px) {
  .makes__body-title {
    font-size: 32px;
    line-height: 40px;
    text-align: center;
    letter-spacing: -0.3px;
  }
}

.makes__body-text {
  max-width: 650px;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
}

.makes__cards .hero__card {
  max-width: 399px;
}
@media (max-width: 1025px) {
  .makes__cards .hero__card {
    max-width: 406px;
  }
}

@media (max-width: 1025px) {
  .makes__body-text--1440 {
    display: none;
  }
}

@media (max-width: 1440px) {
  .makes__body-text--1025 {
    display: none;
  }
}
@media (max-width: 1025px) {
  .makes__body-text--1025 {
    display: block;
    max-width: 646px;
  }
}
@media (max-width: 992.98px) {
  .makes__body-text--1025 {
    text-align: center;
  }
}